.wrapper {
  overflow: hidden;
}
.btn-blue {
  position: relative;
  background: #60A4FF;
  border: 1px solid transparent;
  border-right: 2px solid #1E1E1E;
  border-bottom: 2px solid #1E1E1E;
  border-radius: 10px;
  &:hover {
    background: #FEC636;
    border-color: #1E1E1E;
  }
  span {
    position: relative;
    z-index: 3;
    font-size: 18px;
    line-height: 130%;
    text-transform: uppercase;
  }
}
.btn-yellow {
  position: relative;
  background: #FEC636;
  border: 1px solid transparent;
  border-right: 2px solid #1E1E1E;
  border-bottom: 2px solid #1E1E1E;
  border-radius: 10px;
  &:hover {
    background: #60A4FF;
    border-color: #1E1E1E;
  }
  span {
    position: relative;
    z-index: 3;
    font-size: 18px;
    line-height: 130%;
    text-transform: uppercase;
  }
}
.btn-yellow__radius {
  width: 186px;
  height: 186px;
  background: #FEC636;
  border: 2px solid #1E1E1E;
  border-right: 3px solid #1E1E1E;
  border-bottom: 3px solid #1E1E1E;
  border-radius: 50%;
  &:hover {
    background: #EBFDFF;
    border: 2px solid #1E1E1E !important;
  }
  span {
    font-size: 18px;
    line-height: 130%;
    font-weight: 500;
    text-transform: uppercase;
  }
}
.btn-gray__radius {
  width: 186px;
  height: 186px;
  background: #EBFDFF;
  border: 2px solid #1E1E1E;
  border-right: 3px solid #1E1E1E;
  border-bottom: 3px solid #1E1E1E;
  border-radius: 50%;
  &:hover {
    background: #FEC636;
    border: 2px solid #1E1E1E !important;
  }
  span {
    font-size: 18px;
    line-height: 130%;
    font-weight: 500;
    text-transform: uppercase;
  }
}
h2 {
  font-size: 56px;
  line-height: 100%;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: -1px;
  span {
    font-family: 'Pershotravneva55';
    font-size: 64px;
    color: #FEC636;
    text-stroke: 0.5px #1E1E1E;
    -webkit-text-stroke: 0.5px #1E1E1E;
  }
}
.card-bg {
  position: relative;
  border-radius: 30px;
  &:after {
    content: '';
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #60A4FF;
    border-radius: 30px;
    pointer-events: none;
  }
  &:before {
    content: '';
    position: absolute;
    top: 2px;
    left: 0;
    width: calc(100% + 2px);
    height: 100%;
    background: #1E1E1E;
    border-radius: 30px;
    pointer-events: none;
  }
  .card-lines {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    img {
      width: 100%;
      height: 100%;
    }
  }
  div {
    position: relative;
    z-index: 2;
  }
}
.card-bg__img {
  position: relative;
  border-radius: 30px;
  &:before {
    content: '';
    position: absolute;
    top: 2px;
    left: 0;
    width: calc(100% + 2px);
    height: 100%;
    background: #1E1E1E;
    border-radius: 30px;
    pointer-events: none;
  }
  img {
    position: relative;
    z-index: 2;
    width: 100%;
    border-radius: 30px;
  }
}
.swiper-button-prev,
.swiper-button-next {
  width: 70px;
  height: 70px;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #60A4FF;
  border-right: 1px solid #1E1E1E;
  border-bottom: 1px solid #1E1E1E;
  border-radius: 50%;
  &:after {
    display: none;
  }
  &:hover {
    background: #FEC636;
  }
  svg {
    width: 31px;
    height: auto;
  }
}
.swiper-pagination {
  &-bullet {
    width: 9px;
    height: 9px;
    background: transparent;
    border: 1px solid #1E1E1E;
    border-right: 2px solid #1E1E1E;
    border-bottom: 2px solid #1E1E1E;
    opacity: 1;
    &-active {
      background: #60A4FF;
    }
  }
}
.form {
  display: grid;
  grid-template-columns: 100%;
  gap: 10px;
  &-group {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #EBFDFF;
      border-radius: 10px;
    }
    &:before {
      content: '';
      position: absolute;
      top: 2px;
      left: 0;
      width: calc(100% + 2px);
      height: 100%;
      background: #1E1E1E;
      border-radius: 10px;
    }
    input {
      position: relative;
      z-index: 2;
      width: 100%;
      height: 54px;
      background: transparent;
      border: none;
      border-radius: 10px;
      padding: 0 20px;
      font-size: 18px;
      line-height: 130%;
      color: #1E1E1E;
      &::placeholder {
        color: #989898;
      }
    }
  }
  &-check {
    position: relative;
    min-height: 32px;
    display: flex;
    align-items: center;
    margin-top: 10px;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check.svg");
      }
    }
    label {
      position: relative;
      font-size: 14px;
      line-height: 130%;
      padding-left: 44px;
      &:after {
        content: '';
        position: absolute;
        z-index: 1;
        top: -8px;
        left: 0;
        width: 32px;
        height: 32px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: 80%;
        background-color: #EBFDFF;
        border-radius: 3px;
        transition: all 0.3s;
      }
      &:before {
        content: '';
        position: absolute;
        top: -6px;
        left: 2px;
        width: 32px;
        height: 32px;
        background: #1E1E1E;
        border-radius: 3px;
      }
      a {
        position: relative;
        z-index: 3;
        &:hover {
          color: #60A4FF;
        }
      }
    }
  }
  &-btn {
    padding: 15px;
    margin-top: 10px;
  }
}
.top-anim {
  transform: translate(0, -80%);
  opacity: 0;
  transition: all 0.6s;
}
.top-anim.active {
  transform: translate(0, 0);
  opacity: 1;
}
.bottom-anim {
  transform: translate(0, 80%);
  opacity: 0;
  transition: all 0.6s;
}
.bottom-anim.active {
  transform: translate(0, 0);
  opacity: 1;
}
.right-anim {
  transform: translate(80%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.right-anim.active {
  transform: translate(0, 0);
  opacity: 1;
}
.left-anim {
  transform: translate(-80%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.left-anim.active {
  transform: translate(0, 0);
  opacity: 1;
}
.modal {
  background: rgba(96, 164, 255, 0.8);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: 1060px;
    max-width: 100%;
    background: #EBFDFF;
    border-radius: 30px;
    padding: 50px;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      max-width: calc(100% - 30px);
    }
  }
  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    background: transparent;
    padding: 0;
    width: 46px;
    img {
      width: 100%;
    }
  }
  &__content {
    position: relative;
    z-index: 3;
  }
  &__title {
    max-width: 780px;
    text-align: center;
    margin: 0 auto;
  }
  .form {
    max-width: 385px;
    margin: 40px auto 0;
    &-group {
      input {
        background: #DFF2F8;
      }
    }
  }
  .bg-lines {
    position: absolute;
    z-index: 2;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    width: 100%;
    height: 100%;
    pointer-events: none;
    img {
      width: 100%;
      height: 100%;
    }
  }

}

@media screen and (max-width: 1200px) {
  h2 {
    font-size: 42px;
    span {
      font-size: 50px;
    }
  }
}

@media screen and (max-width: 992px) {
  h2 {
    font-size: 30px;
    span {
      font-size: 40px;
    }
  }
}

@media screen and (max-width: 768px) {
  .modal {
    &__dialog {
      padding: 80px 20px 50px;
    }
  }
}

@media screen and (max-width: 399px) {
  h2 {
    font-size: 28px;
    span {
      font-size: 36px;
    }
  }
}