.main {
  padding-bottom: 230px;
  &__inner {
    position: relative;
    border-radius: 64px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 100%;
      background-image: url("../img/main-bg.svg");
      background-repeat: no-repeat;
      background-position: center bottom;
      background-size: cover;
      border-radius: 64px;
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      top: 1px;
      left: 50%;
      transform: translate(-50%, 0);
      width: calc(100% - 2px);
      height: calc(100% - 6px);
      background-image: url("../img/main-bg2.svg");
      background-repeat: no-repeat;
      background-position: center bottom;
      background-size: cover;
      border-radius: 64px;
      pointer-events: none;
    }
  }
  &__wrap {
    position: relative;
    z-index: 3;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    border-radius: 64px;
    padding: 160px 10px 260px;
  }
  &__title {
    max-width: 1300px;
    font-size: 56px;
    line-height: 100%;
    font-weight: 700;
    letter-spacing: 1px;
    text-transform: uppercase;
    margin: 0 auto;
    .font-span {
      font-family: 'Pershotravneva55';
      font-size: 64px;
      color: #FEC636;
      text-stroke: 0.5px #1E1E1E;
      -webkit-text-stroke: 0.5px #1E1E1E;
    }
    .border-span {
      display: inline-block;
      border: 1px solid #1E1E1E;
      border-right: 3px solid #1E1E1E;
      border-bottom: 3px solid #1E1E1E;
      border-radius: 15px;
      padding: 4px 15px;
    }
  }
  &__list {
    position: relative;
    z-index: 1;
    max-width: 1300px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin: 100px auto 0;
    li {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EBFDFF;
      border: 1px solid transparent;
      border-right: 2px solid #1E1E1E;
      border-bottom: 2px solid #1E1E1E;
      border-radius: 10px;
      padding: 20px 55px;
      transition: all 0.3s;
      &:hover {
        background: #FEC636;
        border-color: #1E1E1E;
        border-radius: 50px;
        &:after,
        &:before {
          display: none;
        }
      }
    }
    li a {
      position: relative;
      z-index: 3;
      width: 100%;
      height: 100%;
      text-align: center;
      font-size: 18px;
      line-height: 130%;
      font-weight: 500;
    }
  }
  &__img {
    position: absolute;
    top: 160px;
    right: -160px;
    width: 332px;
    pointer-events: none;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  &__line {
    position: absolute;
    top: 25px;
    right: 0;
    width: 98px;
    pointer-events: none;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  &__line2 {
    position: absolute;
    left: 0;
    bottom: 154px;
    width: 77px;
    pointer-events: none;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  .bg-lines {
    position: absolute;
    z-index: 2;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    width: 100%;
    height: 100%;
    pointer-events: none;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__btn {
    position: absolute;
    left: 50%;
    bottom: -107px;
    transform: translate(-50%, 0);
  }
}
.advantages {
  padding-bottom: 65px;
  &__top {
    display: flex;
    justify-content: space-between;
    gap: 12px;
    .advantage-card {
      width: calc(100% - 672px);
    }
    .advantage-card__desc {
      width: 58%;
    }
    .advantage-card__img {
      width: 42%;
    }
  }
  &__group {
    width: 660px;
  }
  &__name {
    transform: rotate(-2deg);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border: 2px solid #1E1E1E;
    border-radius: 10px;
    padding: 16px 65px;
    i {
      width: 35px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 18px;
      line-height: 130%;
      font-weight: 500;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 20px / 2));
    gap: 20px;
    margin-top: 20px;
  }
}
.advantage-card {
  width: 100%;
  display: flex;
  align-items: stretch;
  gap: 20px;
  &__desc {
    width: 68%;
    padding: 20px 45px;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    p {
      font-size: 18px;
      line-height: 130%;
      font-weight: 500;
    }
  }
  &__number {
    width: 43px;
    height: 43px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #EBFDFF;
    border: 1px solid #1E1E1E;
    border-right: 2px solid #1E1E1E;
    border-bottom: 2px solid #1E1E1E;
    border-radius: 50px;
    font-size: 18px;
    line-height: 130%;
    font-weight: 500;
    margin-bottom: 80px;
  }
  &__title {
    font-family: 'Pershotravneva55';
    font-size: 34px;
    line-height: 120%;
    color: #FEC636;
    text-stroke: 0.5px #1E1E1E;
    -webkit-text-stroke: 0.5px #1E1E1E;
    margin-bottom: 20px;
  }
  &__img {
    width: 32%;
    flex-shrink: 0;
    img {
      width: 100%;
      height: 100%;
      border-radius: 30px;
    }
  }
}
.info {
  position: relative;
  padding: 65px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 30px;
  }
  &__cards {
    position: relative;
    z-index: 2;
    max-width: 790px;
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
  }
  &__img {
    position: absolute;
    left: 0;
    bottom: 200px;
    width: calc((100vw - 1660px) / 2 + 900px);
    img {
      width: 100%;
    }
  }
}
.info-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 50px;
  padding: 20px 30px 20px 20px;
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 20px;
    li {
      position: relative;
      font-size: 18px;
      line-height: 130%;
      font-weight: 500;
      padding-left: 20px;
      &:after {
        content: '';
        position: absolute;
        z-index: 1;
        top: 6px;
        left: 0;
        width: 10px;
        height: 10px;
        background: #EBFDFF;
        border-radius: 50%;
      }
      &:before {
        content: '';
        position: absolute;
        top: 7px;
        left: 1px;
        width: 10px;
        height: 10px;
        background: #1E1E1E;
        border-radius: 50%;
      }
    }
  }
  &__title ,
  &__price {
    font-family: 'Pershotravneva55';
    font-size: 34px;
    line-height: 120%;
    color: #FEC636;
    text-stroke: 0.5px #1E1E1E;
    -webkit-text-stroke: 0.5px #1E1E1E;
  }
  &__price {
    margin-top: 20px;
  }
  &__btn {
    position: relative;
    z-index: 2;
    flex-shrink: 0;
  }
}
.gallery {
  padding: 65px 0;
  &__slider {
    position: relative;
    margin-top: 50px;
  }
  .swiper {
    overflow: unset;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &-active,
    &-active + .swiper-slide-next,
    &-active + .swiper-slide-next + .swiper-slide {
      opacity: 1;
      pointer-events: auto;
    }
  }
  &__slider-nav {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
    margin-top: 58px;
  }
  .swiper-button-prev,
  .swiper-button-next {
    position: static;
    margin-top: 0;
  }
  .swiper-pagination {
    position: static;
    width: auto;
  }
}
.gallery-card {
  position: relative;
  &__img {
    height: 100%;
    img {
      height: 100%;
    }
  }
  &__location {
    position: absolute;
    z-index: 2;
    left: 50%;
    bottom: -32px;
    transform: translate(-50%, 120%) rotate(-2deg);
    width: max-content;
    height: auto;
    display: inline-flex;
    align-items: center;
    gap: 10px;
    background: #EBFDFF;
    border: 1px solid #1E1E1E;
    border-right: 2px solid #1E1E1E;
    border-bottom: 2px solid #1E1E1E;
    border-radius: 10px;
    padding: 18px 36px;
    i {
      width: 21px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 18px;
      line-height: 130%;
      font-weight: 500;
    }
  }
  &__location.active {
    transform: translate(-50%, 0) rotate(-2deg);
    opacity: 1;
  }
}
