@media screen and (max-width: 1800px) {
  .main {
    &__wrap {
      overflow: hidden;
    }
    &__img {
      top: unset;
      left: 1px;
      right: unset;
      bottom: 44px;
      width: 147px;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
    &__line {
      top: unset;
      bottom: 0;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
