.footer {
  background: #1E1E1E;
  padding: 15px 0;
  .brevis {
    width: 116px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    display: flex;
    flex-wrap: wrap;
    gap: 15px 150px;
    p {
      font-size: 14px;
      line-height: 130%;
      font-weight: 500;
      color: #EBFDFF;
    }
  }
}