$menu-width: 680px;
.header {
  padding: 15px 0;
  &--active {
    .menu {
      left: 0;
    }
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 78px;
  }
  .menu-btn {
    position: relative;
    z-index: 7;
    background: transparent;
    padding: 0;
    transition: all 0.5s;
    //&:after {
    //  content: '';
    //  position: absolute;
    //  top: 50%;
    //  left: 50%;
    //  transform: translate(-50%, -50%);
    //  width: 116px;
    //  height: 116px;
    //  background: #FEC636;
    //  opacity: 0;
    //}
    span {
      position: relative;
      z-index: 2;
      width: 54px;
      height: 5px;
      display: block;
      background: #60A4FF;
      border-radius: 5px;
      &:not(:last-child) {
        margin-bottom: 9.5px;
      }
      &:after {
        content: '';
        position: absolute;
        top: -1px;
        left: 50%;
        transform: translate(-50%, 0);
        width: calc(100% - 3px);
        height: 2px;
        background: #26261F;
        border-radius: 5px;
      }
    }
  }
  .menu-btn.active {
    transform: translate3d($menu-width, 0, 0);
    &:after {
      opacity: 1;
    }
  }
  &__phone {
    display: flex;
    align-items: center;
    gap: 6px;
    &:hover {
      i svg .svg-path {
        fill: #FEC636;
      }
      span {
        text-decoration: underline;
        text-underline-offset: 2px;
      }
    }
    i {
      width: 34px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
      svg .svg-path {
        transition: all 0.3s;
      }
    }
    span {
      font-size: 18px;
      line-height: 130%;
      font-weight: 500;
      transition: all 0.3s;
    }
  }
  &__logo {
    display: block;
    width: 366px;
    img {
      width: 100%;
    }
  }
  &__btn {
    padding: 15px 42px;
  }
  &__list {
    width: 100%;
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 40px;
    li {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EBFDFF;
      border: 1px solid transparent;
      border-right: 2px solid #1E1E1E;
      border-bottom: 2px solid #1E1E1E;
      border-radius: 10px;
      padding: 12px;
      transition: all 0.3s;
      &:hover {
        background: #FEC636;
        border-color: #1E1E1E;
        border-radius: 50px;
      }
    }
    li a {
      position: relative;
      z-index: 3;
      width: 100%;
      height: 100%;
      text-align: center;
      font-size: 18px;
      line-height: 130%;
      font-weight: 500;
    }
  }
  &__form-wrap {
    margin-top: 40px;
  }
  &__form-title {
    font-size: 56px;
    line-height: 100%;
    font-weight: 700;
    text-transform: uppercase;
    span {
      font-family: 'Pershotravneva55';
      font-size: 64px;
      color: #60A4FF;
      text-stroke: 0.5px #1E1E1E;
      -webkit-text-stroke: 0.5px #1E1E1E;
    }
  }
  .form {
    margin-top: 20px;
  }
}
.menu {
  position: absolute;
  z-index: 6;
  top: 0;
  left: -105%;
  width: $menu-width;
  height: 100%;
  background: #FEC636;
  padding: 16px 10px 75px;
  transition: all 0.45s;
  overflow-y: auto;
  @media screen and (min-width: 1661px) {
    width: calc((100vw - 1660px) / 2 + 680px);
  }
  &::-webkit-scrollbar {
    width: 5px;
  }
  &::-webkit-scrollbar-track {
    background: #EBFDFF;
    border-radius: 3px;
  }
  &::-webkit-scrollbar-thumb {
    background: #1E1E1E;
    border-radius: 3px;
  }
  &-wrap {
    max-width: 390px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
  }
  .header__phone {
    margin-top: 10px;
  }
}

@media screen and (max-width: 1400px) {
  .header {
    &__group {
      gap: 16px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    .menu-btn {
      transform: unset !important;
      &:after {
        display: none;
      }
    }
    &__phone,
    &__btn {
      display: none;
    }
    &__logo {
      width: 290px;
    }
    &__form-title {
      font-size: 32px;
      span {
        font-size: 42px;
      }
    }
  }
  .menu {
    width: 100%;
    .header__phone {
      display: flex;
    }
  }
}

@media screen and (max-width: 576px) {
  .menu {
    &-wrap {
      max-width: 100%;
    }
    .header__logo {
      margin-left: auto;
    }
  }
}

@media screen and (max-width: 399px) {
  .header {
    &__inner {
      gap: 8px;
    }
    &__logo {
      width: 230px;
    }
  }
}